

import { _decorator, Component, Node,assetManager,Texture2D,SpriteFrame,ImageAsset,sp } from 'cc';
import FileTools from './FileTools';
import { Html5 } from './Html5';

import {Sprite} from 'cc'

const { ccclass, property } = _decorator;

@ccclass('Machine')
export class Machine extends Component {

  @property(Sprite)
  sp: Sprite = null!;


  spineJson :any = null;
  spineAtlas : any = null;
  spineTexture: any = null;


  start() {


    
  }






  onTest() :void {
    console.log('==========点击测试按钮！！');

    let spr = this.sp;

    let self = this;
    /***/
    Html5.HtmlFileInput(function( file : any ){

      console.log('===========选择文件成功-data:',file);

      Html5.LoadImage(file,spr, function( texture: Texture2D ){

        self.spineTexture = texture;


      });


    });
  }


  loadJson() {
    let self = this;
    Html5.HtmlFileInput(function( file : any ){

      FileTools.getInstance().readLocalFile(file,1,function( data : any ){
        console.log('===========读取Json成功-data:',data);
        self.spineJson = data
      });

    });
  }

  loadAtlas() {
    let self = this;
    Html5.HtmlFileInput(function( file : any ){

      FileTools.getInstance().readLocalFile(file,1,function( data : any ){
        console.log('===========读取Atlas成功-data:',data);
        
        self.spineAtlas = data


        self.addSpine();

      });

    });
  }

  loadRemoto () {
    //const base = url;
    //assetManager.loadRemote(base, { ext: '.png' }, (err: Error, texture: Texture2D) => {

    let sp = this.sp;
    let base = 'http://127.0.0.1/image/Socre_CandyMonsters_Scatter.png';
    assetManager.loadRemote(base, (err, asset: ImageAsset) => {
      if (err) return cc.error(err);
      console.log('asset:',asset)

      let texture = new Texture2D();
      texture.image = asset;

      // sp.spriteFrame = new SpriteFrame();
      // sp.spriteFrame.setTexture(texture)
      // sp.spriteFrame._texture = texture
      // sp.spriteFrame._texture = texture.getHtmlElementObj()
      sp.spriteFrame.texture = texture
    })
  }



  private addSpine () {
    let spineNode = new Node();
    spineNode.name = 'newSpine';
    spineNode.setPosition(0,0);
    var skeleton = spineNode.addComponent(sp.Skeleton);

    // skeleton.parent = this.node;
    cc.find("Canvas").addChild(spineNode);

    let asset = new sp.SkeletonData();
    asset.skeletonJson = this.spineJson;
    asset.atlasText = this.spineAtlas;
    asset.textures = [this.spineTexture];
    asset.textureNames = ['xxx1122.png'];


    skeleton.skeletonData = asset;
    skeleton.animation = 'beiji';
    //skeleton._updateSkeletonData();


  }
}



